import type { Metadata } from "next";
import "./globals.css";

export const metadata: Metadata = {
  title: "Apex Vendors | Verified Supplier Links",
  description: "Premium Vendor Links and Suppliers for Resellers",
  icons: {
    icon: "/favicon.ico",
  },
};

export default function RootLayout({
  children,
}: Readonly<{
  children: React.ReactNode;
}>) {
  return (
    <html lang="en">
      <body className="antialiased relative bg-background overflow-x-hidden">
        {/* 
           Gradients: 
           Using increased opacity (0.15-0.2) to ensure they are visible on dark backgrounds.
           Using sky/cyan and blue as requested.
        */}
        
        {/* Top Left: Cyan/Sky Blue Gradient */}
        <div className="fixed top-0 left-0 w-[800px] h-[800px] bg-cyan-500/15 rounded-full blur-[150px] -translate-x-1/3 -translate-y-1/3 pointer-events-none z-0" />
        
        {/* Bottom Right: Deep Blue Gradient */}
        <div className="fixed bottom-0 right-0 w-[800px] h-[800px] bg-blue-600/20 rounded-full blur-[150px] translate-x-1/3 translate-y-1/3 pointer-events-none z-0" />
        
        {/* Center/Top Right Accent: Lighter Blue */}
        <div className="fixed top-1/4 right-0 w-[600px] h-[600px] bg-sky-600/10 rounded-full blur-[130px] translate-x-1/2 -translate-y-1/2 pointer-events-none z-0" />

        <div className="relative z-10">
          {children}
        </div>
      </body>
    </html>
  );
}
